@import"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css";*{box-sizing:border-box}body{margin:0;padding:0;height:100vh;background-color:#1a1a1a;color:#fff;font-family:Helvetica,Arial,sans-serif}header{padding:.75rem;padding-left:2rem;padding-right:2rem;background-color:#000;display:flex;align-items:center;justify-content:space-between;position:sticky;top:0;left:0;width:100%;z-index:100}@media screen and (max-width: 660px){header{padding-left:1rem;padding-right:1rem;background-color:rgba(0,0,0,.8)}}header div{gap:.5rem;display:flex;align-items:center}@media screen and (max-width: 660px){header div small{display:none}}header img{height:2.25rem;width:auto}button{padding:.5rem 1rem;border:1px solid gray;border-radius:.15rem;background-color:rgba(0,0,0,0);color:inherit;text-transform:uppercase;outline:none}button:hover{background-color:#2e2e2e}button.btn-red{background-color:#e10510;border:none}button.btn-red:hover{background-color:#f90612}footer{padding-bottom:3rem;color:#a6a6a6;padding-left:2rem;padding-right:2rem}footer ul{padding-left:0;margin:2rem 0;list-style:none;display:grid;grid-template-columns:repeat(4, 1fr);row-gap:1rem}@media screen and (max-width: 660px){footer ul{grid-template-columns:repeat(2, 1fr)}}footer ul li{font-size:smaller}footer div{display:inline-block;position:relative}footer div::before{position:absolute;top:33%;left:.5rem;font-family:"FontAwesome";content:""}footer div::after{position:absolute;top:30%;right:.5rem;font-family:"FontAwesome";content:"";pointer-events:inherit}footer div select{padding:1rem 2rem;border:1px solid gray;border-radius:.2rem;background-color:#000;outline:none;color:inherit;appearance:none}.fallback{position:fixed;top:0;left:0;width:100vw;height:100vh;z-index:500;background:#1a1a1a}.fallback div{position:relative;top:calc(50% - 64px);left:calc(50% - 64px)}.fallback div span{position:absolute;width:32px;background:linear-gradient(to top, rgba(225, 5, 16, 0.5), hsl(357deg, 95%, 50%));animation:wave 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}@keyframes wave{0%{top:16px;height:128px}50%,100%{top:48px;height:64px}}.fallback div span:nth-child(1){left:16px;animation-delay:-0.24s}.fallback div span:nth-child(2){left:64px;animation-delay:-0.12s}.fallback div span:nth-child(3){left:112px;animation-delay:0}